import { request_util } from '@/service';
//请求文章
export function getArticleList(data) {
    return request_util.post({
        url: '/article/getlist',
        data: data
    });
}
//文章增加访客
export function addArticleCount(data) {
    return request_util.post({
        url: '/article/addCount',
        data: data
    });
}
//获取文章md文件
export function getArticleMd(data) {
    return request_util.get({
        url: `/article/${data.categoryName}/${data.title}.md`
    });
}
